@import url(/fonts/fonts.css);
* {
  padding: 0;
  margin: 0;
  border: 0;
  outline: 0;
}

body {
  font-family: "PF Benchmark Pro";
  background-color: #eef8ff;
}

.container {
  width: 90%;
  max-width: 1800px;
  margin: auto;
}
#StatsPanel {
  width: 100%;
  padding-bottom: 25px;
  border-radius: 15px;

  background-color: #fff;
}

#StatsPanel > * {
  padding: 0 25px;
}

#StatsPanel .pokemon-name {
  height: 45px;
  margin-bottom: 20px;

  font-weight: 600;
  font-size: 25px;

  display: flex;
  align-items: flex-end;
  justify-content: center;
}

#StatsPanel .info-wrapper {
  text-align: left;
  font-size: 18px;
  overflow-y: auto;
  max-height: calc(100vh - 145px);
  text-align: center;
}

#StatsPanel .info-block {
  margin-bottom: 15px;
  text-align: left;
}


#StatsPanel .info-block .title {
  font-size: 20px;
  font-weight: 600;
  margin-bottom: 5px;
}

#StatsPanel .info-block .stat-name {
  margin-left: 7px;
}

#StatsPanel .info-block .stat-value {
  font-weight: 200;
  float: right;
}

#StatsPanel .info-block .abil-name {
  margin-bottom: 5px;
  font-weight: 500;
}

#StatsPanel .info-block .abil-descr {
  margin-left: 5px;
  margin-bottom: 10px;
}
#AsidePanel {
  min-width: 330px;
  width: 330px;
  position: -webkit-sticky;
  position: sticky;
  top: 25px;

  margin-right: 30px;
  padding-bottom: 25px;
}
.Card {
  width: 150px;
  
  padding: 15px 25px;
  margin: 25px 15px;
  margin-top: 0;
  border-radius: 15px;

  box-shadow: 0px 0px 44px -13px transparent;
  background-color: rgba(255, 255, 255, 0.6);
  -webkit-transform: translate(0, 10px);
          transform: translate(0, 10px);
  -webkit-transition: box-shadow 0.2s ease, background-color 0.2s ease, -webkit-transform 0.3s ease-in-out;
  transition: box-shadow 0.2s ease, background-color 0.2s ease, -webkit-transform 0.3s ease-in-out;
  transition: box-shadow 0.2s ease, background-color 0.2s ease, transform 0.3s ease-in-out;
  transition: box-shadow 0.2s ease, background-color 0.2s ease, transform 0.3s ease-in-out, -webkit-transform 0.3s ease-in-out;
}

.Card:hover,
.Card.active {
  cursor: pointer;
  box-shadow: 0px 8px 44px -15px rgba(0,0,0,0.15);
  background-color: white;
}

.Card:hover .card-img,
.Card.active .card-img{
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

.Card .card-img-wrapper {
  height: 150px;
  width: 100%;

  display: flex;
  justify-content: center;
  align-items: center;
}

.Card .card-img {
  width: 90%;
  -webkit-transition: -webkit-transform 0.3s ease;
  transition: -webkit-transform 0.3s ease;
  transition: transform 0.3s ease;
  transition: transform 0.3s ease, -webkit-transform 0.3s ease;
}

.Card .card-name {
  font-weight: 600;
  font-size: 20px;
}

.Card .card-types {
  height: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.Card .card-types .card-type {
  max-width: 20px;
  max-height: 20px;
  padding: 5px;
  -webkit-transition: margin 0.2s ease-out;
  transition: margin 0.2s ease-out;
}

.Card .card-types .card-type:hover {
  cursor: help;
  margin: 0 5px;
}
.PokeTable {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: flex-start;
}
#CardsNumSelector {
  height: 100%;
  padding: 0 18px;
  border-radius: 15px;

  font-weight: 400;
  font-size: 20px;
  color: #dbe4e9;

  display: flex;
  align-items: center;

  background: white;
}

#CardsNumSelector .card-num {
  cursor: pointer;
}

#CardsNumSelector .sep {
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

#CardsNumSelector > .card-num:hover {
  text-decoration: underline;
}

#CardsNumSelector > .card-num.active {
  font-weight: 500;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  cursor: default;
  text-decoration: none;
}
#MainField .top-panel {
  height: 60px;
  padding: 0 15px;
  margin-bottom: 25px;
  display: flex;
  justify-content: space-between;
}

#MainField .bottom-panel {
  height: 60px;
  margin-top: 20px;
  display: flex;
  justify-content: center;
}
#Home .container {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: flex-start;
}
#App {
  text-align: center;
  color: #222;
}

#AppHeader {
  padding: 30px 0;
}

#AppFooter {
  padding-top: 50px;
  padding-bottom: 20px;
}

#AppFooter a {
  text-decoration: none;
  color: inherit;
}

#AppFooter a:hover{
  text-decoration: underline; 
}
